@media print {
    .fc {
        max-width: 100%!important
    }

    .fc-event {
        background: #fff!important;
        color: #000!important;
        page-break-inside: avoid
    }

    .fc-event .fc-resizer {
        display: none
    }

    .fc hr,.fc tbody,.fc td,.fc th,.fc thead,.fc-row {
        border-color: #ccc!important;
        background: #fff!important
    }

    .fc-bg,.fc-bgevent-container,.fc-bgevent-skeleton,.fc-business-container,.fc-highlight-container,.fc-highlight-skeleton,.fc-mirror-container,.fc-mirror-skeleton {
        display: none
    }

    .fc tbody .fc-row {
        height: auto!important;
        min-height: 0!important
    }

    .fc tbody .fc-row .fc-content-skeleton {
        position: static;
        padding-bottom: 0!important
    }

    .fc tbody .fc-row .fc-content-skeleton tbody tr:last-child td {
        padding-bottom: 1em
    }

    .fc tbody .fc-row .fc-content-skeleton table {
        height: 1em
    }

    .fc-more,.fc-more-cell {
        display: none!important
    }

    .fc tr.fc-limited {
        display: table-row!important
    }

    .fc td.fc-limited {
        display: table-cell!important
    }

    .fc-popover {
        display: none
    }

    .fc-time-grid {
        min-height: 0!important
    }

    .fc-timeGrid-view .fc-axis {
        display: none
    }

    .fc-slats,.fc-time-grid hr {
        display: none!important
    }

    .fc-time-grid .fc-content-skeleton {
        position: static
    }

    .fc-time-grid .fc-content-skeleton table {
        height: 4em
    }

    .fc-time-grid .fc-event-container {
        margin: 0!important
    }

    .fc-time-grid .fc-event {
        position: static!important;
        margin: 3px 2px!important
    }

    .fc-time-grid .fc-event.fc-not-end {
        border-bottom-width: 1px!important
    }

    .fc-time-grid .fc-event.fc-not-end:after {
        content: "..."
    }

    .fc-time-grid .fc-event.fc-not-start {
        border-top-width: 1px!important
    }

    .fc-time-grid .fc-event.fc-not-start:before {
        content: "..."
    }

    .fc-time-grid .fc-event .fc-time {
