.fc-time-grid .fc-content-col {
    position: relative
}

.fc-time-grid .fc-content-skeleton {
    position: absolute;
    z-index: 3;
    top: 0;
    left: 0;
    right: 0
}

.fc-time-grid .fc-business-container {
    position: relative;
    z-index: 1
}

.fc-time-grid .fc-bgevent-container {
    position: relative;
    z-index: 2
}

.fc-time-grid .fc-highlight-container {
    position: relative;
    z-index: 3
}

.fc-time-grid .fc-event-container {
    position: relative;
    z-index: 4
}

.fc-time-grid .fc-now-indicator-line {
    z-index: 5
}

.fc-time-grid .fc-mirror-container {
    position: relative;
    z-index: 6
}

.fc-time-grid .fc-slats td {
    height: 1.5em;
    border-bottom: 0
}

.fc-time-grid .fc-slats .fc-minor td {
    border-top-style: dotted
}

.fc-time-grid .fc-highlight-container {
    position: relative
}

.fc-time-grid .fc-highlight {
    position: absolute;
    left: 0;
    right: 0
}

.fc-ltr .fc-time-grid .fc-event-container {
    margin: 0 2.5% 0 2px
}

.fc-rtl .fc-time-grid .fc-event-container {
    margin: 0 2px 0 2.5%
}

.fc-time-grid .fc-bgevent,.fc-time-grid .fc-event {
    position: absolute;
    z-index: 1
}

.fc-time-grid .fc-bgevent {
    left: 0;
    right: 0
}

.fc-time-grid-event {
    margin-bottom: 1px
}

.fc-time-grid-event-inset {
    -webkit-box-shadow: 0 0 0 1px #fff;
    box-shadow: 0 0 0 1px #fff
}

.fc-time-grid-event.fc-not-start {
    border-top-width: 0;
    padding-top: 1px;
    border-top-left-radius: 0;
    border-top-right-radius: 0
}

.fc-time-grid-event.fc-not-end {
    border-bottom-width: 0;
    padding-bottom: 1px;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0
}

.fc-time-grid-event .fc-content {
    overflow: hidden;
    max-height: 100%
}

.fc-time-grid-event .fc-time,.fc-time-grid-event .fc-title {
    padding: 0 1px
}

.fc-time-grid-event .fc-time {
    font-size: .85em;
    white-space: nowrap
}

.fc-time-grid-event.fc-short .fc-content {
    white-space: nowrap
}

.fc-time-grid-event.fc-short .fc-time,.fc-time-grid-event.fc-short .fc-title {
    display: inline-block;
    vertical-align: top
}

.fc-time-grid-event.fc-short .fc-time span {
    display: none
}

.fc-time-grid-event.fc-short .fc-time:before {
    content: attr(data-start)
}

.fc-time-grid-event.fc-short .fc-time:after {
    content: " - "
}

.fc-time-grid-event.fc-short .fc-title {
    font-size: .85em;
    padding: 0
}

.fc-time-grid-event.fc-allow-mouse-resize .fc-resizer {
    left: 0;
    right: 0;
    bottom: 0;
    height: 8px;
    overflow: hidden;
    line-height: 8px;
    font-size: 11px;
    font-family: monospace;
    text-align: center;
    cursor: s-resize
}

.fc-time-grid-event.fc-allow-mouse-resize .fc-resizer:after {
    content: "="
}

.fc-time-grid-event.fc-selected .fc-resizer {
    border-radius: 5px;
    border-width: 1px;
    width: 8px;
    height: 8px;
    border-style: solid;
    border-color: inherit;
    background: #fff;
    left: 50%;
    margin-left: -5px;
    bottom: -5px
}

.fc-time-grid .fc-now-indicator-line {
    border-top-width: 1px;
    left: 0;
    right: 0
}

.fc-time-grid .fc-now-indicator-arrow {
    margin-top: -5px
}

.fc-ltr .fc-time-grid .fc-now-indicator-arrow {
    left: 0;
    border-width: 5px 0 5px 6px;
    border-top-color: transparent;
    border-bottom-color: transparent
}

.fc-rtl .fc-time-grid .fc-now-indicator-arrow {
    right: 0;
    border-width: 5px 6px 5px 0;
    border-top-color: transparent;
    border-bottom-color: transparent
}

.fc.fc-bootstrap a {
    text-decoration: none
}

.fc.fc-bootstrap a[data-goto]:hover {
    text-decoration: underline
}

.fc-bootstrap hr.fc-divider {
    border-color: inherit
}

.fc-bootstrap .fc-today.alert {
    border-radius: 0
}

.fc-bootstrap a.fc-event:not([href]):not([tabindex]) {
    color: #fff
}

.fc-bootstrap .fc-popover.card {
    position: absolute
}

.fc-bootstrap .fc-popover .card-body {
    padding: 0
}

.fc-bootstrap .fc-time-grid .fc-slats table {
    background: 0 0
}


.fc-day-grid-event .fc-time {
    display: block
}

.fc-day-grid-event .fc-content {
    padding: .25rem .5rem
}

.fc-popover .fc-header {
    padding: .5rem .75rem
}

.fc-more-popover .fc-event-container {
    padding: .5rem
}

.fc-popover .fc-header .fc-title {
    font-weight: 700
}

.fc-toolbar h2 {
    font-size: 1rem;
    font-weight: 500;
    text-transform: uppercase
}

.fc-event,
.fc-event-dot {
    background: transparent;
	text-align: left;
}

.fc-event {
    border: 1px solid #7a59ad;
	padding: 0.25rem 0.5rem;
}

.fc-day-top.fc-other-month {
    opacity: 1
}

.fc-other-month {
    background-image: linear-gradient(135deg,rgba(0,0,0,.02) 25%,transparent 25%,transparent 50%,rgba(0,0,0,.02) 50%,rgba(0,0,0,.02) 75%,transparent 75%,transparent);
    background-color: #fafcfd;
    background-size: 1rem 1rem
}

.fc-head-container thead tr {
    background-image: -webkit-gradient(linear,left bottom, left top,color-stop(0, #f2f2f2),to(#fafafa));
    background-image: linear-gradient(to top,#f2f2f2 0,#fafafa 100%)
}

.fc .fc-non-business {
	--fc-non-business-color: transparent;
}

.fc-day-other {
    background-image: linear-gradient(135deg,rgba(var(--bs-body-color-rgb),0.02)25%,transparent 25%,transparent 50%,rgba(var(--bs-body-color-rgb),0.02)50%,rgba(var(--bs-body-color-rgb),0.02)75%,transparent 75%,transparent);
    background-color: rgba(var(--bs-primary-rgb), 0.05);
    background-size: 1rem 1rem;
}

.table-calendar .fc-scrollgrid-section-body td {
	padding: 0 !important;
	border: 1px solid var(--bs-border-color) !important;
}

.fc .fc-toolbar-title {
	font-size: 1rem;
    font-weight: 600;
}

.fc-daygrid-block-event .fc-event-time {
    font-weight: 500;
}

.fc-theme-bootstrap a:not([href]) {
	overflow: hidden;
}

thead[role="presentation"] .fc-scrollgrid-sync-inner {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;

    background-image: linear-gradient(to top, rgba(var(--bs-body-color-rgb), 0.02) 0, rgba(var(--bs-body-color-rgb), 0.05) 100%);
}

.fc table thead th {
    padding: 0 !important;
    border: 1px solid var(--bs-border-color) !important;
    border-bottom: 0 !important;
}


.fc-daygrid-dot-event {
  display: flex;
  flex-wrap: wrap;
  align-items: center; 
}

.fc-daygrid-event-dot,
.fc-event-time {
    display: inline-block;
    margin-right: 0.25rem;
    margin-left: 0;
}

.fc-event-title {
  flex: 1 0 100%; 
  margin-top: 0; 
}


@media only screen and (max-width: 768px) {
    .fc-day-grid-event .fc-time {
        display:inline
    }

    .fc-bootstrap:not(.fc-reset-order) .fc-toolbar {
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
        -ms-flex-direction: column;
        flex-direction: column
    }

    .fc-bootstrap:not(.fc-reset-order) .fc-toolbar h2 {
        font-size: 1.125rem;
        font-weight: 500
    }

    .fc-bootstrap:not(.fc-reset-order) .fc-toolbar .fc-left {
        -webkit-box-ordinal-group: 2;
        -ms-flex-order: 1;
        order: 1
    }

    .fc-bootstrap:not(.fc-reset-order) .fc-toolbar .fc-right {
        -webkit-box-ordinal-group: 3;
        -ms-flex-order: 2;
        order: 2
    }

    .fc-bootstrap:not(.fc-reset-order) .fc-toolbar .fc-center {
        -webkit-box-ordinal-group: 4;
        -ms-flex-order: 3;
        order: 3
    }

    .fc-bootstrap:not(.fc-reset-order) .fc-toolbar>div:not(:empty):not(:first-child) {
        margin-top: .5rem
    }
}
